import React from 'react';
import styles from './styles.module.scss'
import Button from '@material-ui/core/Button';
import ContainerAuth from "../../../../common/c4-containerAuth";
import SuperCheckbox from '../../../../common/c3-SuperCheckbox/SuperCheckbox';


type ModalType = {
    openModal: boolean;
    setOpenModal: (bool: boolean) => void;
    setActionTC: (value?: string) => void;
    type: string;
    question: string
    setType: (string: string) => void
    answer: string
}

const ModalTable = ({openModal, setOpenModal, setActionTC, type, question, setType, answer}: ModalType) => {
    const typeBoolean = type === 'show'

    const targetContainerExit = (e: any) => {
        if (e.target.closest('#modal') !== null) return
        setOpenModal(false)
    }

    return (
        <ContainerAuth className={`${!openModal && styles.hidden} ${styles.box}`} onClick={targetContainerExit}>
            <div className={styles.modal} id={'modal'}>

                <div className={styles.pBox}>
                    <div>
                        <div className={styles.question}>
                            <h2>Question:</h2> <p>{question}?</p>
                        </div>
                        {!typeBoolean && (
                            <div className={styles.answer}>
                                <h2>Answer:</h2> <p>{answer}</p>
                            </div>
                        )}
                    </div>
                </div>

                {!typeBoolean && (
                    <div className={styles.checkBoxBox}>
                        <h1>Rate yourself:</h1>
                        <div className={styles.boxInputCheck}>
                            <div className={styles.checkbox}><SuperCheckbox /><span>Did not know </span></div>
                            <div className={styles.checkbox}><SuperCheckbox /><span>Forgot</span></div>
                            <div className={styles.checkbox}><SuperCheckbox /><span>Confused</span></div>
                            <div className={styles.checkbox}><SuperCheckbox /><span>Knew the answer</span></div>
                        </div>

                    </div>
                )
                }

                <div className={styles.btn}>
                    <Button style={{borderRadius: '20px', background: '#21268F', color: '#fff',height:'40px',width:'150px'   }}
                            variant="contained"
                            onClick={setType.bind(null, '')}>{typeBoolean ? 'Show answer' : 'Next'}</Button>
                    <Button style={{borderRadius: '20px', background: '#D7D8EF', color: '#21268F',height:'40px',width:'150px'}}
                            onClick={setOpenModal.bind(null, false)}
                            variant="outlined">Cancel</Button>
                </div>

            </div>
        </ContainerAuth>
    );
}
// color="success"
// color="error"
export default ModalTable
