import React, {useState} from 'react';
import styles from './styles.module.scss'
import {useDispatch} from "react-redux";
import {setCardID} from "../../../profileReducer";
import Rating from '@material-ui/lab/Rating';
import ModalTable from "../t3-modalTable";

const Rows = ({row, typeTableRequest, profileID, buttonHandler, setCardName}: any) => {
    const dispatch = useDispatch()
    const getCardId = (id: string, name: string) => {
        dispatch(setCardID(id))
        setCardName(name)
    }
    const [openModal, setOpenModal] = useState(false)
    const [type, setType] = useState('')

    const actionLearnHandler = () => {
        setOpenModal(true)
        setType('show')
    }
    return <div className={styles.rowe} key={row._id}>
        <ModalTable openModal={openModal} setOpenModal={setOpenModal} question={row.question} setActionTC={() => {
        }} type={type} setType={setType} answer={row.answer}/>
        <div className={styles.row}>

            <span className={styles.rowItem} id={'table'}>{typeTableRequest ? row.name : row.question}</span>
            <span className={styles.rowItem}
                  id={'table'}>{typeTableRequest ? row.cardsCount : (

                <div className={`${styles.rowItem} ${styles.btnBox}`}>
                    {profileID === row.user_id &&
                    (<>
                        <span className={styles.btn} data-color>Delete</span>
                        <span className={styles.btn}>Edit</span>
                    </>)
                    }
                    <span className={styles.btn} onClick={actionLearnHandler}>Answer</span>
                </div>

            )}</span>
            <span className={styles.rowItem} id={'table'}>{row.updated.slice(0, 10)}</span>
            <span className={styles.rowItem}
                  id={'table'}> {typeTableRequest ? row.user_name :
                <Rating name="read-only" value={row.grade} readOnly/>}</span>

            {typeTableRequest && <div className={`${styles.rowItem} ${styles.btnBox}`}>
                {profileID === row.user_id &&
                (<>
                    <span className={styles.btn} data-color
                          onClick={buttonHandler.bind(null, row._id, 'delete')}>Delete</span>
                    <span className={styles.btn}
                          onClick={buttonHandler.bind(null, row._id, 'edit')}>Edit</span>
                </>)
                }
                <span onClick={getCardId.bind(null, row._id, row.name)}
                      className={styles.btn}>Learn</span>
            </div>}

        </div>
    </div>

};

export default Rows;
